import { Box, Typography } from "@mui/material";
import React from "react";

const experience = [
  {
    name: "Talkout",
    description: [
      " Worked as part of a small team to develop a VR content delivery platform for the Pico Neo 3 VR headset.",
      " Developed a VR Fire Extinguisher Safety training module for UK Fire Group.",
      " Worked with Halfords to recreate their eMobility/eBike training in a virtual reality environment.",
    ],
    skills: ["Unity3D", "C#", "Java", "Android Native"],
  },
  {
    name: "Virtual Reality Simulation Systems",
    description: [
      " Developed virtual reality 360 tours for the University of Wolverhampton, Great Western Railway and the Martin James Foundation.",
      " Created a Stop and Search training experience for GWENT Police Constabulary.",
    ],
    skills: ["Unity3D", "C#", "Xcode", "HTML", "CSS"],
  },
];

const ExperienceCard = (name, description, skills) => {
  return (
    <Box
      sx={{
        flexGrow: 1,
        bgcolor: "#14172F",
        p: 2,
        border: "1px solid #413f4f",
        borderRadius: "8px",
      }}
    >
      <Typography
        sx={{
          flexGrow: 1,
          fontFamily: "Radio Canada, sans-serif",
          fontSize: "1.25rem",
          fontWeight: "600",
          color: "white",
        }}
      >
        {name}
      </Typography>
      {description.map((d) => (
        <Typography
          sx={{
            flexGrow: 1,
            fontFamily: "Radio Canada, sans-serif",
            fontSize: "1.25rem",
            fontWeight: "600",
            color: "white",
          }}
        >
          {d}
        </Typography>
      ))}
      <Box
        sx={{
          py: 2,
          display: "inline-flex",
          justifyContent: "center",
          flexWrap: "wrap",
          gap: 2,
        }}
      >
        {skills.map((skill) => (
          <Typography
            sx={{
              flexGrow: 1,
              fontFamily: "Radio Canada, sans-serif",
              fontSize: "1.25rem",
              fontWeight: "600",
              color: "white",
            }}
          >
            {skill}
          </Typography>
        ))}
      </Box>
    </Box>
  );
};

const Experience = () => {
  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        flexDirection: "column",
        justifyContent: "center",
        gap: 2,
        px: 2,
        py: 10,
        textAlign: "center",
        bgcolor: "#0F172A",
        flexShrink: 1,
      }}
    >
      <Typography
        sx={{
          flexGrow: 1,
          fontFamily: "Radio Canada, sans-serif",
          fontSize: "2.5rem",
          fontWeight: "700",
          color: "white",
          textTransform: "uppercase",
        }}
      >
        Experience
      </Typography>
      <Box
        sx={{
          py: 2,
          display: "flex",
          justifyContent: "center",
          flexWrap: "wrap",
          gap: 2,
        }}
      >
        {experience.map((exp) =>
          ExperienceCard(exp.name, exp.description, exp.skills)
        )}
      </Box>
    </Box>
  );
};

export default Experience;
